import axios from "axios";

// const baseUrl = "http://localhost:3001";
const baseUrl = "http://192.241.132.27:3007"; // Nginx proxy endpoint

export default function usePost() {
  async function addNewDiagnosis(data) {
    await axios
      .post(baseUrl + baseUrl + "/api/diagnoses", data)
      .then(res => console.log(res))
      .catch(err => console.log(err));

    alert("ADDED Diagnosis.");
  }

  async function updateDiagnosis(id, data) {
    await axios
      .patch(baseUrl + `/api/diagnoses/${id}`, data)
      .then(res => console.log(res))
      .catch(err => console.log(err));

    alert("UPDATED Diagnosis.");
  }

  async function deleteDiagnosis(id) {
    await axios
      .delete(baseUrl + `/api/diagnosis/${id}`)
      .then(res => console.log(res))
      .catch(err => console.log(err));

    alert("DELETED Diagnosis.");
  }

  async function addNewSymptom(data) {
    await axios
      .post(baseUrl + "/api/symptoms", data)
      .then(res => console.log(res))
      .catch(err => console.log(err));

    alert("ADDED Symptom.");
  }

  async function updateSymptom(id, data) {
    await axios
      .patch(baseUrl + `/api/symptoms/${id}`, data)
      .then(res => console.log(res))
      .catch(err => console.log(err));

    alert("UPDATED Symptom.");
  }

  async function deleteSymptom(id) {
    await axios
      .delete(baseUrl + `/api/symptoms/${id}`)
      .then(res => console.log(res))
      .catch(err => console.log(err));
  }

  return {
    addNewDiagnosis,
    updateDiagnosis,
    deleteDiagnosis,
    addNewSymptom,
    updateSymptom,
    deleteSymptom
  };
}
